
#app {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
}
.clickable {
  cursor: pointer;
}

/* Basic mobile styling */
@media screen and (max-width: 640px) {
  /* probably hide drafted table */
}

/* banner */
.hero {
  height: auto; /* grows according to text - won't need updating if I move the instructions*/
  background: url(/ffb-draft-magic-vue/static/img/ffb-banner.b908899.jpg);
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  color: white;
  /* font-size: x-large;*/
}
.hero h1 {
  color: #fff;
  text-shadow: 0px 1px 0px #999, 0px 2px 0px #888, 0px 3px 0px #777, 0px 4px 0px #666, 0px 5px 0px #555, 0px 6px 0px #444, 0px 7px 0px #333, 0px 8px 7px #001135;
  font: xx-large 'ChunkFiveRegular';
}

/* for headers and labels */
.orange {
  border: none;
  padding: 0 0 0 5px;
  color: orange;
  font-weight: bold;
  background-color: #ffffff;
}

/* grid settings*/
.container {
  display: grid;
  grid-template-columns: 50% 25%;
}
.item {
  margin: 1em;
}

/* general table styles since b-table styling isnt working ... */
table {
  min-width: 100%;
  background-color: #ffffff; /* in case page bg color is different */
}
table, tr, td {
  border: 1px solid black;
  border-collapse: collapse;
}
th {
  font-weight: bold;
  background-color: #d6d6d6;
}
td {
  padding: 1px 2px 1px 2px;
}
tr:hover {
  background-color: #d6d6d6;
}

.upload-button {
  position: relative;
  overflow: hidden;
  display: inline-block;
  background-color: #ffffff;
  color: orange;
  border: 2px solid black;
  border-radius: 5px;
  padding: 8px 12px;
  cursor: pointer;
}
.upload-button:hover {
  background-color: #d6d6d6;
}
.upload-button input {
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
  opacity: 0;
}
